<script>
import ExpansionPanels from "@/components/ExpansionPanels";
import Panel from "@/components/ExpansionPanels/Panel.vue";
import TextField from "@/components/ExpansionPanels/TextField.vue";
import TextareaField from "@/components/ExpansionPanels/TextareaField.vue";

export default {
  components: {
    ExpansionPanels, Panel, TextField, TextareaField
  },

  props: ["loading", "value", "meta"],

  emits: ["onSave", "input"],

  data: () => ({
    openedPanels: [0, 1, 2, 3, 4, 5],
    formIsValid: undefined
  }),

  computed: {
    model: {
      get() {
        return this.value;
      },
      set(v) {
        this.$emit("input", v);
      }
    }
  }
}
</script>
