<template>
  <fragment>
    <LoadingOverlay v-if="loading"/>

    <PhrasesSettingsForm
        v-model="localBotPhrases"
        :meta="$store.getters.botPhrasesMeta"
        :loading="loading"
        @onSave="save"
    />
  </fragment>
</template>

<script>
import {Fragment} from "vue-fragment";
import LoadingOverlay from "@/components/Loading/LoadingOverlay.vue";
import PhrasesSettingsForm from "@/components/PhrasesSettingsForm.vue"

export default {
  components: {
    Fragment, LoadingOverlay, PhrasesSettingsForm
  },

  data: () => ({
    loading: true,
    localBotPhrases: {}
  }),

  methods: {
    async save() {
      this.loading = true;
      await this.$store.dispatch("putBotPhrases", this.localBotPhrases);
      this.loading = false;
    }
  },

  async mounted() {
    document.title = this.$t("Настройки фраз бота");
    await this.$store.dispatch("getBotPhrases");
    await this.$store.dispatch("getBotPhrasesMeta");
    this.localBotPhrases = JSON.parse(JSON.stringify(this.$store.getters.botPhrases));
    this.loading = false;
  }
}
</script>
