<template>
  <v-textarea
      dense outlined filled
      :label="label" :disabled="disabled" :rules="rules"
      v-model="model" v-on="$listeners"
  />
</template>

<script>
export default {
  computed: {
    model: {
      get() {
        return this.value
      },
      set(v) {
        this.$emit('input', v)
      }
    }
  },

  props: ["value", "label", "disabled", "rules"]
}
</script>